import React, {useState, useEffect} from 'react'
import '../PageContent/PageContent.css'
import mainLogo from '../Assets/imgs/cat.jpg'
import secondLogo from '../Assets/imgs/dog.jpg'
import thirdLogo from '../Assets/imgs/happy_dog.jpg'
import fourthLogo from '../Assets/imgs/mean_cat.jpg'
import fifthLogo from '../Assets/imgs/swipe.jpg'
import sixthLogo from '../Assets/imgs/dog.jpg'


let imgArr = [mainLogo, secondLogo, thirdLogo, fourthLogo, fifthLogo, sixthLogo]
let item = imgArr[Math.floor(Math.random() * imgArr.length)]



const PageContent = () => {

    let api = ""

    const getLocation = () => {
        if (navigator.geolocation)
            navigator.geolocation.getCurrentPosition(showPosition)
        
        else {
            document.body.innerHTML = "Geolocation not supported."
            api = "https://weather-proxy.freecodecamp.rocks/api/current?lat=35&lon=139"
        }
            
    }

    const showPosition = (position) => {
        api = `https://weather-proxy.freecodecamp.rocks/api/current?lat=${Math.round(position.coords.latitude)}&lon=${Math.round(position.coords.longitude)}`
    } 

    getLocation()
    
    

    // Current date
    var curr = new Date();
    curr.setDate(curr.getDate());
    var date = curr.toISOString().substr(0,10);

    // USE states

    const [celciusClick, setCelciusClick] = useState(false)
    const [farClick, setFarClick] = useState(true)

    const [weatherInfo, setWeatherInfo] = useState({
        location: "",
        weatherType: "",
        degrees: "",
        pressure: "",
        humidity: "",
        wind: "",
        icon: "",
        original: ""
    })

useEffect(() => {
fetch(api)
.then((res) => res.json())
.then((obj)=>{
    console.log(obj)
    // variables to avoid verbose code
    var pressure = obj['main'].pressure
    var weatherType = obj['weather'][0].description.toLowerCase().split(' ').map((s) => s.charAt(0).toUpperCase() + s.substring(1)).join(' ');
    var humidity = obj['main'].humidity + "%"
    var wind = Math.round(obj['wind'].speed) + " mph"
    var degress = Math.round(obj['main'].temp) 
    var icon = obj['weather'][0].icon 
    var original = Math.round(obj['main'].temp) 
    var location = obj.name

    // set info together
    setWeatherInfo({
        weatherType: weatherType,
        degrees: degress,
        pressure: pressure,
        humidity: humidity,
        wind: wind,
        icon: icon,
        original: original,
        location: location
    })


})
}, [])

const original = weatherInfo.original
// convert Far to Celcius .
const HandleCelcius = () => {
    setFarClick(false)
   setCelciusClick(true)

    
    
    weatherInfo.degrees = ((weatherInfo.degrees - 32) * 5 / 9).toFixed(4)
 
}
// convert celcius .
const HandleFar = () => {
    setCelciusClick(false)
    setFarClick(true)

    weatherInfo.degrees = original
 
}

return (
    <div className="grid">
        <div className="weather-info">
            <h1>{weatherInfo.location}</h1>
            <h4>{weatherInfo.weatherType} <img src={weatherInfo.icon} height="35"></img> </h4>
            
            <input type="date" id="date" defaultValue={date} />
            <div>  
                {weatherInfo.degrees}&#176;
                <button onClick={HandleFar} disabled={farClick}>F</button><button onClick={HandleCelcius} disabled={celciusClick}>C</button>
            </div>
        </div>
        
            <div className="extra-info">
        <table>
            <tbody>
        <tr>
            <td>Pressure: {weatherInfo.pressure}</td>
        </tr>
        <tr>
            <td>Humidity: {weatherInfo.humidity}</td>
        </tr>
        <tr>
            <td>Wind: {weatherInfo.wind}</td>
        </tr>
        </tbody>
        </table>
        <div className="imgs">
                <div className="img">
                    <figure>
                        <img src={item} width="150" />
                    </figure>
                </div>
            </div>
            </div>

            
                
    </div>
)





}






export default PageContent